<template>
  <div class="wrapper" v-if="users.length > 0">
    <header>
      <h1 v-if="users.length > 0">{{actualUser.name}}</h1>
      <select v-model='idUserProp'>
        <option v-for="(opt) in users" :value='opt._id' v-bind:key='opt._id'>{{opt.name}}</option>
      </select>
    </header>
    <div id="create" v-if="users.find(usr => usr._id == (idUserProp || idUser)).role == 'ind'">
      <hr>
      <h2>Criar Seed</h2>
      <form action="">
        <label for="nome">Nome:</label>
        <input @input="getName($event.target.value)" name='nome' type="text" placeholder="Digite aqui o nome do seed">
        <br>
        <label for="tipo">Tipo:</label>
        <input type="radio" v-model="type" id="reais" name="tipo" value="money">
        <label for="reais">Reais (R$)</label>
        <input type="radio" v-model="type" id="promo" name="tipo" value="points">
        <label for="promo">Pontos</label>
        <br>
        <label for="valor">Valor:</label>
        <input @input="getPrice($event.target.value)" name="valor" type="number" placeholder="Digite o valor">
        <br>
        <button v-on:click="createSeed">Criar</button>
      </form>
    </div>
    <template v-if="Object.keys(actualUser).length > 0">
      <div id="frac" v-if="(Object.keys(actualUser.seeds).length > 0) && (seeds.length > 0)">
        <hr>
        <h2>Fracionar Seed</h2>
        <form action="">
          <label for="seed">Seed:</label>
          <select v-model="fracIdCamp">
            <option v-for="(camp, key) in actualUser.seeds" v-bind:key="key" :value="key">{{camp.seed_name}}</option>
          </select>
          <br>
          <label for="dest">Destino:</label>
          <select v-model="fracIdUser">
            <option v-for="(merc) in users.filter(usr => (usr._id != idUserProp && usr._id != seeds.find(sd => sd._id == fracIdCamp).owner_id))" v-bind:key="merc._id" :value="merc._id">{{merc.name}}</option>
          </select>
          <br>
          <label for="valor">Valor:</label>
          <input name="valor" type="number" placeholder="Digite o valor" v-model="fracValue" :max="seeds.find(sd => sd._id == fracIdCamp).value - seeds.find(sd => sd._id == fracIdCamp).used">
          <br>
          <button v-on:click="fracSeed">Salvar</button>
        </form>
      </div>
      <div id="extrato" v-if="(Object.keys(actualUser.seeds).length > 0) && (seeds.length > 0)">
        <hr>
        <h2>Extrato</h2>
        <label for="seed">Selecione o Seed:</label>
        <select v-model="frac">
          <option v-for="(camp, key) in actualUser.seeds" v-bind:key="key" :value="key">{{camp.seed_name}}</option>
        </select>
        <div class="results" v-if="actualUser.seeds[frac]">
          <div class="valores" v-if="actualUser._id == seeds.find(sd => sd._id == fracIdCamp).owner_id">
            <h4>Total do Seed:</h4>
            <span>{{parseFloat(seeds.find(seed => seed._id == frac).value).toLocaleString('pt-BR', (actualUser.seeds[frac].seed_type == 'points' ? {} : { style: 'currency', currency: 'BRL', minimumFractionDigits: 2, maximumFractionDigits: 2 }))}}</span>
            <span v-if="seeds.find(seed => (seed._id == frac)).type == 'points'"> Pontos</span>
          </div>
          <div class="valores">
            <h4>{{(actualUser._id == seeds.find(sd => sd._id == fracIdCamp).owner_id ? 'Parcial do Seed:' : 'Total do Seed:')}}</h4>
            <span>{{(actualUser.seeds[frac].seed_creator ? parseFloat(actualUser.seeds[frac].seed_used).toLocaleString('pt-BR', (actualUser.seeds[frac].seed_type == 'points' ? {} : { style: 'currency', currency: 'BRL', minimumFractionDigits: 2, maximumFractionDigits: 2 })) : parseFloat(actualUser.seeds[frac].seed_funds).toLocaleString('pt-BR', (actualUser.seeds[frac].seed_type == 'points' ? {} : { style: 'currency', currency: 'BRL', minimumFractionDigits: 2, maximumFractionDigits: 2 })))}}</span>
            <span v-if="actualUser.seeds[frac].seed_type == 'points'"> Pontos</span>
          </div>
        </div>
      </div>
    </template>
  </div>
</template>

<script>
  export default {
    name: 'card',
    props: ['idUser'],
    data: () => ({
      apiKey: '?apiKey=LLKamk80CzsWhh1DYeLENAKd0-vPMjN_',
      seedUrl: 'https://api.mlab.com/api/1/databases/gestao-valores/collections/seeds',
      userUrl: 'https://api.mlab.com/api/1/databases/gestao-valores/collections/usuarios',
      idUserProp: '',
      seeds: [],
      users: [],
      actualUser: '',
      fracIdCamp: '',
      fracIdUser: '',
      fracValue: '',
      message: '',
      price: '',
      type: '',
      frac: ''
    }),
    watch: {
      idUserProp: function(val) {
        this.actualUser = (this.users.length > 0) ? this.users.find(user => user._id == val) : '';
        this.fracIdCamp = (Object.keys(this.actualUser.seeds).length > 0) ? Object.keys(this.actualUser.seeds)[0] : 'Nenhuma campanha cadastrada'
        let ownerIdSeed = (!this.seeds.find(sd => sd._id == this.fracIdCamp) ? val : this.seeds.find(sd => sd._id == this.fracIdCamp).owner_id)
        this.fracIdUser = this.users.filter(usr => (usr._id != val && usr._id != ownerIdSeed))[0]._id
        this.frac = (Object.keys(this.actualUser.seeds).length > 0) ? Object.keys(this.actualUser.seeds)[0] : '--'
        this.fracValue = ''
      }
    },
    methods: {
      reloadAllCards: function() {
        console.log('call reloadAll')
        this.$root.$emit('reloadCards')
      },
      async loadValues(userReload = this.idUser, valueReload = false) {
        await fetch(this.userUrl + this.apiKey)
          .then(response => response.json())
          .then(users => {
            var sort = users.sort( (a,b) => a._id - b._id );
            // console.log(sort)
            this.users = sort;
          })
        await fetch(this.seedUrl + this.apiKey)
          .then(response => response.json())
          .then(seeds => {
            var sort = seeds.sort( (a,b) => a._id - b._id );
            // console.log(sort)
            this.seeds = sort;
          })

        // OBJETO DO USUARIO ATUAL
        this.idUserProp = userReload;
        this.actualUser = this.users.find(user => user._id == (userReload));

        this.users.forEach(user => {
          user['seeds'] = {}
          user.created_seeds.forEach(sd => {
            if (!user['seeds'][sd.seed_id]) {
              user['seeds'][sd.seed_id] = {
                'seed_name': this.seeds.find(seed => seed._id == sd.seed_id).campanha,
                'seed_value': sd.seed_value,
                'seed_used': this.seeds.find(seed => seed._id == sd.seed_id).used,
                'seed_type': this.seeds.find(seed => seed._id == sd.seed_id).type,
                'seed_creator': true,
                'seed_sender': false,
                'seed_funds': 0,
                'seed_history': []
              }
            }
          })
          user.participated_seeds.forEach(sd => {
            if (!user['seeds'][sd.participated_seeds_id]) {
              user['seeds'][sd.participated_seeds_id] = {
                'seed_name': this.seeds.find(seed => seed._id == sd.participated_seeds_id).campanha,
                'seed_value': this.seeds.find(seed => seed._id == sd.participated_seeds_id).value,
                'seed_used': this.seeds.find(seed => seed._id == sd.participated_seeds_id).used,
                'seed_type': this.seeds.find(seed => seed._id == sd.participated_seeds_id).type,
                'seed_owner_id': this.seeds.find(seed => seed._id == sd.participated_seeds_id).owner_id,
                'seed_creator': false,
                'seed_sender': true,
                'seed_funds': parseFloat(sd.participated_seeds_value),
                'seed_history': [
                {
                  'send_by': this.users.find(us => us._id == sd.participated_seeds_sender_id).name,
                  'value': sd.participated_seeds_value,
                  'date': sd.participated_seeds_date || ''
                }
                ]
              }
            } else {
              user['seeds'][sd.participated_seeds_id]['seed_funds'] = parseFloat(user['seeds'][sd.participated_seeds_id]['seed_funds']) +  parseFloat(sd.participated_seeds_value)
              user['seeds'][sd.participated_seeds_id]['seed_history'].push({
                'send_by': this.users.find(us => us._id == sd.participated_seeds_sender_id).name,
                'value': sd.participated_seeds_value,
                'date': sd.participated_seeds_date || ''
              })
            }
          })
        })
        // if (!valueReload) {
          this.fracIdCamp = (this.actualUser.created_seeds.length > 0) ? this.actualUser.created_seeds[0].seed_id : 'Nenhuma campanha cadastrada'
          this.fracIdUser = this.users.filter(usr => usr._id != this.idUserProp)[0]._id
        // }
        this.frac = (Object.keys(this.actualUser.seeds).length > 0) ? Object.keys(this.actualUser.seeds)[0] : '--'
        this.fracValue = ''
      },
      async init() {
        await this.loadValues();
      },
      getName(value) {
        this.message = value;
      },
      getPrice(value) {
        this.price = value;
      },
      getUniqueIds(arrFull) {
        var arr1 = arrFull,
        merged = [];

        [arr1].forEach((hash => a => a.forEach(b => {
          if (!hash[(b.seed_id || b.participated_seeds_id)]) {
            hash[(b.seed_id || b.participated_seeds_id)] = {};
            merged.push(hash[(b.seed_id || b.participated_seeds_id)]);
          }
          Object.assign(hash[(b.seed_id || b.participated_seeds_id)], b);
        }))(Object.create(null)));


        return merged;
      },
      async createSeed(e) {
        e.preventDefault();
        await fetch(this.seedUrl + this.apiKey, {
          method: 'POST',
          body: JSON.stringify({
            "_id": (Math.max.apply(null, (this.seeds.map(iid => iid._id).length > 0 ? this.seeds.map(iid => iid._id) : [0]))+1).toString(),
            "campanha": this.message,
            "type": this.type,
            "value": this.price,
            "used": "0",
            "created_date": new Date(),
            "owner_id": this.idUserProp,
            "cashout": false,
            "participated_users":[]
          }),
          headers: new Headers({'content-type': 'application/json'}),
        })
        .then(response => {
          alert('gravou!');
          console.log('gravou seed ==> ', response)
        })
        let tmpArr = this.users.find(user => user._id == this.idUserProp).created_seeds;
        tmpArr.push({'seed_id': (Math.max.apply(null, (this.seeds.map(iid => iid._id).length > 0 ? this.seeds.map(iid => iid._id) : [0]))+1).toString(), 'seed_value': this.price});
        await fetch(this.userUrl + '/' + this.idUserProp + this.apiKey, {
          method: 'PUT',
          body: JSON.stringify({
            "$set": {"created_seeds": [...tmpArr]}
          }),
          headers: new Headers({'content-type': 'application/json'}),
        })
        .then(response => {
          alert('gravou!')
          console.log('gravou no usuário ==> ', response)
        })
        await this.reloadAllCards();
      },
      async fracSeed(e) {
        e.preventDefault();
        let tmpArrSeed = this.seeds.find(sdd => sdd._id == this.fracIdCamp);
        let prtUser = (tmpArrSeed.participated_users.indexOf(this.fracIdUser) < 0) ? [...tmpArrSeed.participated_users, this.fracIdUser] : [...tmpArrSeed.participated_users],
            vlUsed = parseFloat(tmpArrSeed.used) + parseFloat(this.fracValue),
            tmpArrUser = this.users.find(sdd => sdd._id == this.fracIdUser).participated_seeds,
            tmpArrSender = this.actualUser.participated_seeds,
            setBodySeed = (this.actualUser.seeds[this.fracIdCamp].seed_creator ? {"participated_users": [...prtUser], "used": vlUsed} : {"participated_users": [...prtUser]});

        tmpArrUser.push({"participated_seeds_id": this.fracIdCamp,"participated_seeds_value": this.fracValue,"participated_seeds_sender_id": this.idUserProp})
        tmpArrSender.push({"participated_seeds_id": this.fracIdCamp,"participated_seeds_value": (this.fracValue * -1),"participated_seeds_sender_id": this.idUserProp})

        await fetch(this.seedUrl + '/' + this.fracIdCamp + this.apiKey, {
          method: 'PUT',
          body: JSON.stringify({
            "$set": setBodySeed
          }),
          headers: new Headers({'content-type': 'application/json'}),
        })
        .then(response => {
          alert('atualizou!')
          console.log('atualizou o seed ==> ', response)
        })
        await fetch(this.userUrl + '/' + this.fracIdUser + this.apiKey, {
          method: 'PUT',
          body: JSON.stringify({
            "$set": {"participated_seeds": [...tmpArrUser]}
          }),
          headers: new Headers({'content-type': 'application/json'}),
        })
        .then(response => {
          alert('atualizou!')
          console.log('atualizou o usuário ==> ', response)
        })
        if (this.actualUser.seeds[this.fracIdCamp].seed_sender) {
          await fetch(this.userUrl + '/' + this.idUserProp + this.apiKey, {
            method: 'PUT',
            body: JSON.stringify({
              "$set": {"participated_seeds": [...tmpArrSender]}
            }),
            headers: new Headers({'content-type': 'application/json'}),
          })
          .then(response => {
            alert('atualizou!')
            console.log('atualizou o usuário ==> ', response)
          })
        }
        await this.reloadAllCards();
      }
    },
    mounted () {
      this.$root.$on('reloadCards', async () => {
        console.log('call reloadTHIScard', this.idUserProp)
        await this.loadValues(this.idUserProp, true);
      })
      this.init();
    }
  }
</script>

<style scoped>
@import "~bootstrap/dist/css/bootstrap.min.css";
.wrapper {
  display: inline-block;
  width: 30vw;
  height: 100%;
  border: solid 1px lightgray;
  margin: 1vw 1vw auto;
  padding-top: 25px;
  padding-bottom: 25px;
}
#extrato .results .valores {
  margin-top: 20px;
  display: inline-block;
  width: 49%;
  border-right: solid 1px lightgray;
}
#extrato .results .valores:last-child {
  border: none;
}
hr {
  width: 90%;
}
h3 {
  margin: 40px 0 0;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
</style>
